import React, { ReactNode } from "react";
import { ReactElement } from "react";

import { useAppComponentRendererContext } from "@rollouthq/connect-react/components/app-component-renderer/app-component-renderer-context";
import { useAutomationInputParam } from "@rollouthq/connect-react/components/app-component-renderer/use-automation-input-param";
import { dataAttr, Flex, UserForm } from "@rollouthq/ui";

export type ActionInputFormControlProps = {
  bind: any;
  label?: string;
  description?: string;
  error?: string;
  isRequired?: boolean;
  children: ReactElement;
};

export function ActionInputFormControl(props: ActionInputFormControlProps) {
  const { bind, children, description, error, isRequired = false } = props;

  const { credentialId, isBlueprintActivation = false } =
    useAppComponentRendererContext();

  const param = useAutomationInputParam(bind);

  const isProvidedByUserPlaceholder =
    param.isCredentialDependent &&
    credentialId == null &&
    !isBlueprintActivation;

  if (isBlueprintActivation && !param.isCredentialDependent) {
    return null;
  }

  function getChildren() {
    if (isProvidedByUserPlaceholder) {
      return (
        <Flex
          css={{
            alignItems: "center",
            padding: "8px",
            backgroundColor: "$surfaceSecondary",
            borderRadius: "4px",
            color: "$grayMedium",
            fontSize: "13px",
            fontWeight: 500,
          }}
        >
          <UserForm css={{ marginRight: "6px" }} />
          Input provided by user when automation is activated
        </Flex>
      );
    }

    return children;
  }

  const label = props.label ?? "";

  return (
    <StyledActionInputFormControl
      description={description}
      error={error}
      isRequired={isRequired}
      label={label}
    >
      {getChildren()}
    </StyledActionInputFormControl>
  );
}

export type StyledActionInputFormControlProps = {
  children: ReactNode;
  label: string;
  description?: string;
  error: string | undefined;
  isRequired: boolean;
};

export function StyledActionInputFormControl(
  props: StyledActionInputFormControlProps
) {
  const { label, description, error, isRequired, children } = props;

  return (
    <div className="rollout-form-control">
      {label && (
        <div className="rollout-form-control__label">
          {label}
          {isRequired && (
            <span className="rollout-form-control__required-hint">
              {" "}
              (required)
            </span>
          )}
        </div>
      )}

      {description && (
        <div className="rollout-form-control__description">{description}</div>
      )}

      <div
        className="rollout-form-control__input"
        data-error={dataAttr(!!error)}
      >
        {children}
      </div>

      {error && <div className="rollout-form-control__error">{error}</div>}
    </div>
  );
}
