import React, { useEffect, useState } from "react";

import { ActionInputProps } from "@rollouthq/connect-react/components/action-inputs";
import { ActionInputFormControl } from "@rollouthq/connect-react/components/action-inputs/helpers/action-input-form-control";
import { useAppComponentRendererContext } from "@rollouthq/connect-react/components/app-component-renderer/app-component-renderer-context";
import { useAutomationInputParam } from "@rollouthq/connect-react/components/app-component-renderer/use-automation-input-param";
import { useFormContext } from "@rollouthq/connect-react/utils/form";
import { MultiSelect, MultiSelectOption } from "@rollouthq/ui";

export type MultiSelectInputProps = ActionInputProps<string | undefined> & {
  fetchOptions: () => Promise<MultiSelectOption[]> | MultiSelectOption[];
  dependencies?: string[];
};

export function MultiSelectInput(props: MultiSelectInputProps) {
  const { bind, dependencies, fetchOptions, placeholder } = props;
  const param = useAutomationInputParam(bind);
  const { disabled: disabledCtx } = useAppComponentRendererContext();
  const disabled = disabledCtx || !dependencies?.every(Boolean);
  const { value, setValue } = param;
  const label = props.label ?? param.label;
  const description = props.description ?? param.description;
  const [options, setOptions] = useState<MultiSelectOption[]>([]);
  const [isLoadingOptions, setIsLoadingOptions] = useState(false);
  const { shouldShowErrors } = useFormContext();

  useEffect(() => {
    async function getOptions() {
      setIsLoadingOptions(true);
      if (!disabled) {
        const dropDownOptions = await fetchOptions();
        setOptions(dropDownOptions);
      }
      setIsLoadingOptions(false);
    }
    getOptions();
  }, [disabled]);

  return (
    <ActionInputFormControl
      bind={bind}
      label={label}
      description={description}
      error={shouldShowErrors ? param.error?.message : undefined}
    >
      <MultiSelect
        label={label}
        isDisabled={disabled}
        items={options}
        isLoading={isLoadingOptions}
        inputPlaceholder={placeholder || "Select option"}
        onSelectionChange={(items) => {
          setValue(items.map((i) => i.id).join(", "));
        }}
        value={value}
      />
    </ActionInputFormControl>
  );
}
