import React from "react";
import { unreachableCase } from "ts-assert-unreachable";
import { createProxy } from "ts-object-path";
import { AnyZodObject, z } from "zod";

import { useQuery } from "@rollouthq/api-client-react";
import type { CustomerTriggerDropdownInputOption } from "@rollouthq/api/engine/customers/get-customer-trigger-dropdown-options";
import type {
  DropdownInputDefOutput,
  InputDefOutput,
} from "@rollouthq/api/engine/inputs/inputs-configuration";
import { resolveMentions, Variable } from "@rollouthq/common";
import * as inputComponents from "@rollouthq/connect-react/components/action-inputs";
import { StyledActionInputFormControl } from "@rollouthq/connect-react/components/action-inputs/helpers/action-input-form-control";
import {
  ComboBox,
  ComboBoxItem,
  RichEditor,
  TextField,
  useAsyncMemo,
} from "@rollouthq/ui";

import {
  AppComponentRendererContext,
  AppComponentRendererContextProvider,
} from "./app-component-renderer-context";

const b = createProxy<z.infer<AnyZodObject>>();

export type ActionRendererProps = AppComponentRendererContext;

export function AppComponentRenderer(props: ActionRendererProps) {
  const {
    api,
    credentialId,
    inputParams,
    setInputParams,
    importedData,
    variables,
    getDynamicOptions,
  } = props;

  return (
    <AppComponentRendererContextProvider value={props}>
      {"Component" in importedData ? (
        <importedData.Component
          b={b}
          credentialId={credentialId}
          inputParams={inputParams}
          setInputParams={setInputParams}
          api={api}
          useQuery={useQuery}
          inputComponents={inputComponents}
        />
      ) : (
        <>
          {importedData.inputParamDefs.map((def) => {
            return (
              <InputDefRenderer
                key={def.key}
                def={def}
                value={inputParams[def.key]}
                setValue={(newValue) =>
                  setInputParams({
                    ...inputParams,
                    [def.key]: newValue,
                  })
                }
                variables={variables}
                getDynamicOptions={getDynamicOptions}
              />
            );
          })}
        </>
      )}
    </AppComponentRendererContextProvider>
  );
}

function InputDefRenderer(props: {
  def: InputDefOutput;
  value: any;
  setValue: (newValue: any) => void;
  variables: readonly Variable[];
  getDynamicOptions: (opts: {
    inputKey: string;
  }) => Promise<CustomerTriggerDropdownInputOption[]>;
}) {
  const { def, value, setValue, variables, getDynamicOptions } = props;

  function renderInput() {
    if (def.type === "plainText") {
      return (
        <TextField
          className="rollout-input"
          value={value ?? ""}
          onChange={setValue}
        />
      );
    }

    if (def.type === "dropdown") {
      return (
        <DropdownInputDefRenderer
          def={def}
          value={value}
          setValue={setValue}
          getDynamicOptions={getDynamicOptions}
        />
      );
    }

    if (def.type === "string") {
      return (
        <div className="rollout-rich-text-input">
          <RichEditor
            value={value && resolveMentions(value, variables)}
            setValue={setValue}
            formattingEnabled={false}
            linksEnabled={false}
            mentionVariables={variables}
          />
        </div>
      );
    }

    unreachableCase(def);
  }

  return (
    <StyledActionInputFormControl
      label={def.label}
      isRequired
      error={undefined}
    >
      {renderInput()}
    </StyledActionInputFormControl>
  );
}

function DropdownInputDefRenderer(props: {
  def: DropdownInputDefOutput;
  value: string;
  setValue: (newValue: string | undefined) => void;
  getDynamicOptions: (opts: {
    inputKey: string;
  }) => Promise<CustomerTriggerDropdownInputOption[]>;
}) {
  const { def, value, setValue, getDynamicOptions } = props;

  const dropdownOptionsAsync = useAsyncMemo(async () => {
    if (def.dropdownConfiguration?.type === "static") {
      return def.dropdownConfiguration.staticOptions.map((opt) => ({
        key: opt,
        label: opt,
      }));
    } else {
      return await getDynamicOptions({ inputKey: def.key });
    }
  }, [JSON.stringify(def.dropdownConfiguration)]);

  const dropdownOptions = dropdownOptionsAsync.value ?? [];
  const selectedOption = dropdownOptions.find((e) => e.key === value);

  return (
    <ComboBox<typeof dropdownOptions[number]>
      defaultItems={dropdownOptions}
      selectedOption={selectedOption}
      menuTrigger="focus"
      icon={null}
      inputPlaceholder="Select option"
      onSelectionChange={(newSelectedOption) => {
        setValue(newSelectedOption?.key);
      }}
    >
      {(item) => (
        <ComboBoxItem key={item._comboboxKey} textValue={item.label}>
          {item.label}
        </ComboBoxItem>
      )}
    </ComboBox>
  );
}
